.PopupLayout {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #281960c2;
  display: flex;
  align-items: center;
  justify-content: center;

  .PopupLayout_container {
    background-color: white;
    border-radius: 10px;
    max-width: 80%;
  }
}
